html,
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
}

.foot_icon {
  display: flex;
  width: 200px;
  margin: auto;
  justify-content: space-evenly;
  align-items: center;
}

.avatar-text {
  box-shadow: 0px 0px 120px #ffffff50;
  backdrop-filter: blur(5px) saturate(70%);
}

.font-s {
  font-size: 1.15rem;
}

/* CSS styles for non-mobile devices */
@media (min-width: 601px) {
  /* Write your CSS rules here */
  .sticky-large {
    position: sticky !important;
    top: 15px !important;
  }

  /* Other styles for non-mobile devices */
}

@import "https://fonts.googleapis.com/css?family=Open+Sans:600,700";
* {
  font-family: "Open Sans", sans-serif;
}

.link {
  color: rgb(83, 83, 217);
}

table tr {
  border: 1px solid #9a9a9a80;
  line-height: 3vh;
}

table th,
table td {
  padding: 0.625em;
  border-bottom: 0.5px dotted #757575;
  min-width: 100px;
}

table th {
  font-size: 0.85em;
  text-align: left;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #757575 !important;
}

@media only screen and (device-width: 768px) {
  table {
    border: 0;
    box-shadow: none;
  }
}

@media only screen and (min-device-width: 481px) and (max-device-width: 1024px) and (orientation: portrait) {
  table {
    border: 0;
    box-shadow: none;
  }
}

@media only screen and (min-device-width: 481px) and (max-device-width: 1024px) and (orientation: landscape) {
  table {
    border: 0;
    box-shadow: 0px 35px 10vw #565656;
  }
}

@media only screen and (max-width: 600px) {
  table {
    border: 0;
    box-shadow: none;
  }
  table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  table tr {
    border-bottom: 3px solid #00000060;
    border-right: 3px solid #00000040;
    background: linear-gradient(100deg, #aaaaaa00, #fff);
    border-radius: 0 0 4vw 0;
    display: block;
    margin-bottom: 0.625em;
  }
  table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: 0.8em;
    text-align: right;
  }
  table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }
  table td:last-child {
    border-bottom: 0;
  }
}
